import {Component, EventEmitter, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {BsModalRef} from 'ngx-bootstrap/modal';
import {ToastrService} from 'ngx-toastr';
import {TranslateService} from '@ngx-translate/core';
import {Subject} from 'rxjs';

import {RegionsService} from '../../../../core/services/regions.service';
import {HelperService} from '../../../../core/services/helper.service';

@Component({
    selector: 'app-regions-create',
    templateUrl: './regions-create.component.html',
    styleUrls: ['./regions-create.component.scss']
})

export class RegionsCreateComponent implements OnInit {
    public event: EventEmitter<any> = new EventEmitter();
    public onClose: Subject<boolean>;
    formTables: any;
    selectboxOptions: any;
    createRegionsForm: FormGroup;
    isFormSubmitted = false;
    isPageLoaded = false;
    editorOptions = {theme: 'vs-dark', language: 'html'};

    constructor(
        public translate: TranslateService,
        public bsModalRef: BsModalRef,
        private formBuilder: FormBuilder,
        private toastr: ToastrService,
        private regionsService: RegionsService,
        private helperService: HelperService
    ) {
    }

    get regionsControl() {
        return this.createRegionsForm.controls;
    }

    ngOnInit() {
        this.onClose = new Subject();

        this.loadForms();
    }

    loadForms() {
        this.createRegionsForm = this.formBuilder.group({
            name: [null, Validators.required],
            content_html: [null, Validators.required],
            number_content: [null, [Validators.required, Validators.min(1)]],
            description: [null],
        });
    }

    onSubmit() {
        this.isFormSubmitted = true;

        if (this.createRegionsForm.invalid) {
            return;
        }

        this.regionsService.create(this.createRegionsForm.value)
            .subscribe(
                data => {
                    this.toastr.success(this.translate.instant('settings_web.regions.messages.create'), this.translate.instant('settings_web.regions.title'));
                    this.event.emit({data: true});
                    this.onCancel();
                });
    }

    onCancel() {
        this.onClose.next(false);
        this.bsModalRef.hide();
    }
}
