import {Component, EventEmitter, OnInit} from '@angular/core';
import {FormArray, FormBuilder, FormGroup, Validators} from '@angular/forms';
import {BsModalRef} from 'ngx-bootstrap/modal';
import {ToastrService} from 'ngx-toastr';
import {TranslateService} from '@ngx-translate/core';
import {Subject} from 'rxjs';

import {Pages} from '../../../../shared/models/pages.model';

import {PagesService} from '../../../../core/services/pages.service';
import {TemplatesService} from '../../../../core/services/templates.service';
import {HelperService} from '../../../../core/services/helper.service';
import { SeoService } from 'src/app/core/services/seo.service';

@Component({
    selector: 'app-pages-edit',
    templateUrl: './pages-edit.component.html',
    styleUrls: ['./pages-edit.component.scss']
})

export class PagesEditComponent implements OnInit {
    public event: EventEmitter<any> = new EventEmitter();
    public onClose: Subject<boolean>;
    editPagesForm: FormGroup;
    formTables: any;
    selectboxOptions: any;
    templateData: any;
    seoData: any;
    pages: Pages;
    isFormSubmitted = false;
    isPageLoaded = false;
    isCollapsedHead1 = false;
    isCollapsedHead2 = false;
    dataType = [
        {label: 'Single Page', value: 0},
        {label: 'Category Page', value: 1},
        {label: 'Collection Page', value: 2},
        {label: 'Products Page', value: 3},
        {label: 'News Page', value: 4}];
    editorOptions = {theme: 'vs-dark', language: 'html'};

    constructor(
        public translate: TranslateService,
        public bsModalRef: BsModalRef,
        private formBuilder: FormBuilder,
        private toastr: ToastrService,
        private pagesService: PagesService,
        private seoService: SeoService,
        private helperService: HelperService,
        private templatesService: TemplatesService
    ) {

    }

    get pagesControl() {
        return this.editPagesForm.controls;
    }

    ngOnInit() {
        this.onClose = new Subject();
        this.getAllMeta();
        this.getAllTemplates();
        this.loadForms();
    }

    getAllTemplates() {
        this.templatesService.getAll()
            .subscribe(
                data => {
                    this.templateData = data;
                });
    }

    loadForms() {
        this.editPagesForm = this.formBuilder.group({
            id: [this.pages.id],
            name: [this.pages.name, Validators.required],
            description: [this.pages.description],
            templates_id: [this.pages.templates_id, Validators.required],
            title: [this.pages.title, Validators.required],
            type: [this.pages.type, Validators.required],
            seoData: this.formBuilder.array([]),
            seo: [this.pages.seo],
            url: [this.pages.url, Validators.required],
        });
        this.typeChange({value: this.pages.type});
        // this.changeMetaTag(this.pages.seoData);
        for (let seo of this.pages.seoData) {
            this.onAddMetaTag(seo);
        }
        this.isPageLoaded = true;

    }
    getAllMeta() {
        this.seoService.getAllMeta()
            .subscribe(
                data => {
                    this.seoData = data;
                });
    }
    private getSeoData(data) {
        !data.content ? data.content = null : '';
        return this.formBuilder.group({
            meta: [data.label, Validators.required],
            meta_id: [data.id, Validators.required],
            content: [data.content]
        });
    }
    private addSeoData(data) {
        const control = <FormArray>this.editPagesForm.controls['seoData'];
        control.push(this.getSeoData(data));
    }
    onAddMetaTag(even: any) {
        const control = <FormArray>this.editPagesForm.controls['seoData'];
        control.push(this.getSeoData(even));
    }
    onRemoveMetaTag(even: any) {
        (<FormArray>this.editPagesForm.get('seoData')).removeAt(even.index)
    }
    onSubmit() {
        this.isFormSubmitted = true;

        if (this.editPagesForm.invalid) {
            return;
        }

        this.pagesService.update(this.editPagesForm.value)
            .subscribe(
                data => {
                    this.toastr.success(this.translate.instant('settings_web.pages.messages.update'), this.translate.instant('settings_web.pages.title'));
                    this.event.emit({data: true});
                    this.onCancel();
                });
    }

    onCancel() {
        this.onClose.next(false);
        this.bsModalRef.hide();
    }

    typeChange($event) {
        if($event.value != 0){
            this.editPagesForm.get('title').setValidators([]);
            this.editPagesForm.get('title').updateValueAndValidity();
            this.editPagesForm.get('url').setValidators([]);
            this.editPagesForm.get('url').updateValueAndValidity();
        } else {
            this.editPagesForm.get('title').setValidators([Validators.required]);
            this.editPagesForm.get('title').updateValueAndValidity();
            this.editPagesForm.get('url').setValidators([Validators.required]);
            this.editPagesForm.get('url').updateValueAndValidity();
        }
    }
}
