import {Component, OnInit, ViewChild} from '@angular/core';
import {BsModalRef, BsModalService} from 'ngx-bootstrap/modal';
import {HttpClient} from '@angular/common/http';
import {ExportAsConfig, ExportAsService} from 'ngx-export-as';
import {ToastrService} from 'ngx-toastr';
import {TranslateService} from '@ngx-translate/core';
import {DataTableDirective} from 'angular-datatables';
import {Subject} from 'rxjs';
import Swal from 'sweetalert2';
import {ActivatedRoute, Router} from '@angular/router';

import {WidgetsTypesService} from '../../../../core/services/widgets-types.service';
import {HelperService} from '../../../../core/services/helper.service';
import {AuthenticationService} from '../../../../core/services/authentication.service';

import {ManagerWidgetsTypesCreateComponent} from '../../components/manager-widgets-types-create/manager-widgets-types-create.component';
import {ManagerWidgetsTypesEditComponent} from '../../components/manager-widgets-types-edit/manager-widgets-types-edit.component';

import {DatatablesResponse} from '../../../../core/helpers/datatables-response.helper';
import {environment} from '../../../../../environments/environment';

import 'datatables.net';
import 'datatables.net-bs4';

declare let $: any;

@Component({
    selector: 'app-manager-widgets-types',
    templateUrl: './manager-widgets-types.component.html',
    styleUrls: ['./manager-widgets-types.component.scss']
})

export class ManagerWidgetTypesComponent implements OnInit {
    public apiUrl = environment.apiUrl;
    public modalRef: BsModalRef;
    @ViewChild(DataTableDirective, {static: true})
    dtElement: DataTableDirective;
    dtTrigger: Subject<any> = new Subject();
    formTables: any;
    params: any;
    dtOptions: any = {};
    loginUser: any;
    dataWidgetType: any = [];
    managerWidgetTypes: any = [];
    exportAsConfig: ExportAsConfig = {
        type: 'pdf',
        elementIdOrContent: 'custom_field_table',
    };
    modalConfigs = {
        animated: true,
        keyboard: true,
        backdrop: true,
        ignoreBackdropClick: false,
        class: 'inmodal modal-dialog-centered animated fadeIn modal-lg',
        initialState: {}
    };

    constructor(
        public translate: TranslateService,
        private modalService: BsModalService,
        private http: HttpClient,
        private exportAsService: ExportAsService,
        private toastr: ToastrService,
        private authenticationService: AuthenticationService,
        private widgetsTypesService: WidgetsTypesService,
        private router: Router,
        private route: ActivatedRoute,
        private helperService: HelperService
    ) {
        this.authenticationService.loginUser.subscribe(x => this.loginUser = x);
        this.route.paramMap.subscribe(params => {
            this.params = params;
        });
    }

    ngOnInit() {
        // this.getAllFormFields();
        this.loadManagerWidgetTypeDatatable();
    }

    loadManagerWidgetTypeDatatable() {
        let that = this;
        this.dtOptions = {
            pagingType: 'full_numbers',
            widgetsTypeLength: that.loginUser.settings.tables_pagination_limit,
            serverSide: true,
            processing: true,
            dom: '<"html5buttons"B>ltfrtip',
            columns: [{
                'sortable': false,
                'target': [0]
            }, {
                'sortable': false,
                'target': [1]
            }, {
                'sortable': false,
                'target': [2]
            }, {
                'sortable': false,
                'target': [3]
            }, {
                'sortable': false,
                'target': [4]
            }, {
                'sortable': false,
                'width': '5%',
                'target': [4]
            }],
            buttons: [{
                extend: 'csv',
                title: this.translate.instant('settings_web.manager_widgets_types.title'),
                className: 'btn btn-datatable-gredient',
                action: function (e, dt, node, config) {
                    that.exportFiles('csv');
                }
            }, {
                extend: 'excel',
                title: this.translate.instant('settings_web.manager_widgets_types.title'),
                className: 'btn btn-datatable-gredient',
                action: function (e, dt, node, config) {
                    that.exportFiles('xlsx');
                }
            }, {
                extend: 'pdf',
                title: this.translate.instant('settings_web.manager_widgets_types.title'),
                className: 'btn btn-datatable-gredient',
                action: function (e, dt, node, config) {
                    that.exportFiles('pdf');
                }
            }],
            language: {
                sLengthMenu: 'Show _MENU_',
                sSearch: '',
                sSearchPlaceholder: 'Search ...'
            },
            ajax: (dataTablesParameters: any, callback) => {
                dataTablesParameters.widget_type_id = this.params.get('id');
                this.http
                    .post<DatatablesResponse>(this.apiUrl + '/api/all-manager-widgets-types', dataTablesParameters, {})
                    .subscribe(resp => {
                        this.managerWidgetTypes = resp.data;
                        this.dataWidgetType = resp.parentData;
                        callback({
                            recordsTotal: resp.recordsTotal,
                            recordsFiltered: resp.recordsFiltered,
                            data: [],
                        });
                    });
            }
        };
    }

    exportFiles(type) {
        this.exportAsConfig.type = type;
        this.exportAsService.save(this.exportAsConfig, 'managerWidgetType').subscribe(() => {
        });
    }

    ngAfterViewInit(): void {
        this.dtTrigger.next();
    }

    ngOnDestroy(): void {
        this.dtTrigger.unsubscribe();
    }

    rerender(): void {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
            dtInstance.destroy();

            setTimeout(() => {
                this.dtTrigger.next();

                if (this.managerWidgetTypes.length > 0) {
                    $('.tfoot_dt').addClass('d-none');
                } else {
                    $('.tfoot_dt').removeClass('d-none');
                }
            });
        });
    }

    openManagerWidgetTypeCreateModal() {
        let modalConfig = {
            animated: true,
            keyboard: true,
            backdrop: true,
            ignoreBackdropClick: false,
            class: 'inmodal modal-dialog-centered animated fadeIn modal-lg',
            initialState: {
                managerWidgetsTypes: {
                    widget_type_id: this.params.get('id')
                }
            }
        };
        this.modalRef = this.modalService.show(ManagerWidgetsTypesCreateComponent, modalConfig);
        this.modalRef.content.event.subscribe(data => {
            this.rerender();
        });
    }

    openManagerWidgetTypeEditModal(ManagerWidgetTypes) {
        this.widgetsTypesService.getFieldById(ManagerWidgetTypes.id)
            .subscribe(
                resp => {
                    let modalConfig = {
                        animated: true,
                        keyboard: true,
                        backdrop: true,
                        ignoreBackdropClick: false,
                        class: 'inmodal modal-dialog-centered animated fadeIn modal-lg',
                        initialState: {
                            managerWidgetTypes: resp
                        }
                    };
                    this.modalRef = this.modalService.show(ManagerWidgetsTypesEditComponent, modalConfig);
                    this.modalRef.content.event.subscribe(data => {
                        this.rerender();
                    });
                });
    }

    removeManagerWidgetType(id) {
        Swal.fire({
            title: this.translate.instant('common.swal.title'),
            text: this.translate.instant('common.swal.text') + ' ' + this.translate.instant('settings_web.manager_widgets_types.title'),
            type: 'warning',
            showCancelButton: true,
            confirmButtonText: this.translate.instant('common.swal.confirmButtonText'),
            cancelButtonText: this.translate.instant('common.swal.cancelButtonText')
        }).then((result) => {
            if (result.value) {
                this.widgetsTypesService.deleteManagerWidgetsTypes(id)
                    .subscribe(
                        data => {
                            this.toastr.success(this.translate.instant('settings_web.manager_widgets_types.messages.delete'), this.translate.instant('settings_web.manager_widgets_types.title'));
                            this.rerender();
                        });
            }
        });
    }

}

