import {Component, EventEmitter, OnInit} from '@angular/core';
import {FormArray, FormBuilder, FormGroup, Validators} from '@angular/forms';
import {BsModalRef} from 'ngx-bootstrap/modal';
import {ToastrService} from 'ngx-toastr';
import {TranslateService} from '@ngx-translate/core';
import {Subject} from 'rxjs';

import {PagesService} from '../../../../core/services/pages.service';
import {TemplatesService} from '../../../../core/services/templates.service';
import {HelperService} from '../../../../core/services/helper.service';
import { SeoService } from 'src/app/core/services/seo.service';

@Component({
    selector: 'app-pages-create',
    templateUrl: './pages-create.component.html',
    styleUrls: ['./pages-create.component.scss']
})

export class PagesCreateComponent implements OnInit {
    public event: EventEmitter<any> = new EventEmitter();
    public onClose: Subject<boolean>;
    formTables: any;
    selectboxOptions: any;
    createPagesForm: FormGroup;
    isFormSubmitted = false;
    isSinglePage = true;
    templateData: any;
    seoData: any;
    isPageLoaded = false;
    isCollapsedHead1 = false;
    isCollapsedHead2 = false;
    dataType = [
        {label: 'Single Page', value: 0},
        {label: 'Category Page', value: 1},
        {label: 'Collection Page', value: 2},
        {label: 'Products Page', value: 3},
        {label: 'News Page', value: 4}];
    editorOptions = {theme: 'vs-dark', language: 'html'};

    constructor(
        public translate: TranslateService,
        public bsModalRef: BsModalRef,
        private formBuilder: FormBuilder,
        private toastr: ToastrService,
        private pagesService: PagesService,
        private seoService: SeoService,
        private templatesService: TemplatesService,
        private helperService: HelperService
    ) {
    }

    get pagesControl() {
        return this.createPagesForm.controls;
    }

    ngOnInit() {
        this.onClose = new Subject();
        this.getAllTemplates();
        this.getAllMeta();
        this.loadForms();
    }

    getAllTemplates() {
        this.templatesService.getAll()
            .subscribe(
                data => {
                    this.templateData = data;
                });
    }
    getAllMeta() {
        this.seoService.getAllMeta()
            .subscribe(
                data => {
                    this.seoData = data;
                });
    }
    loadForms() {
        this.createPagesForm = this.formBuilder.group({
            name: [null, Validators.required],
            templates_id: [null, Validators.required],
            description: [null],
            title: [null, Validators.required],
            type: [null, Validators.required],
            seoData: this.formBuilder.array([]),
            seo: [null],
            url: [null, Validators.required],
        });
        this.createPagesForm.patchValue({type: 0});
    }
    private getSeoData(data) {
        return this.formBuilder.group({
            meta: [data.label, Validators.required],
            meta_id: [data.id, Validators.required],
            content: [null]
        });
    }
    private addSeoData(data) {
        const control = <FormArray>this.createPagesForm.controls['seoData'];
        control.push(this.getSeoData(data));
    }
    onAddMetaTag(even: any) {
        const control = <FormArray>this.createPagesForm.controls['seoData'];
        control.push(this.getSeoData(even));
    }
    onRemoveMetaTag(even: any) {
        (<FormArray>this.createPagesForm.get('seoData')).removeAt(even.index)
    }
    onSubmit() {
        this.isFormSubmitted = true;

        if (this.createPagesForm.invalid) {
            return;
        }

        this.pagesService.create(this.createPagesForm.value)
            .subscribe(
                data => {
                    this.toastr.success(this.translate.instant('settings_web.pages.messages.create'), this.translate.instant('settings_web.pages.title'));
                    this.event.emit({data: true});
                    this.onCancel();
                });
    }

    onCancel() {
        this.onClose.next(false);
        this.bsModalRef.hide();
    }

    typeChange($event) {
        if($event.value != 0){
            this.createPagesForm.get('title').setValidators([]);
            this.createPagesForm.get('title').updateValueAndValidity();
            this.createPagesForm.get('url').setValidators([]);
            this.createPagesForm.get('url').updateValueAndValidity();
        } else {
            this.createPagesForm.get('title').setValidators([Validators.required]);
            this.createPagesForm.get('title').updateValueAndValidity();
            this.createPagesForm.get('url').setValidators([Validators.required]);
            this.createPagesForm.get('url').updateValueAndValidity();
        }
    }
}
