import {Component, EventEmitter, OnInit} from '@angular/core';
import {FormArray, FormBuilder, FormGroup, Validators} from '@angular/forms';
import {BsModalRef} from 'ngx-bootstrap/modal';
import {ToastrService} from 'ngx-toastr';
import {TranslateService} from '@ngx-translate/core';
import {Subject} from 'rxjs';

import {ManagerWidgetsTypes} from '../../../../shared/models/manager-widgets-types.model';

import {WidgetsTypesService} from '../../../../core/services/widgets-types.service';
import {WidgetsService} from '../../../../core/services/widgets.service';
import {HelperService} from '../../../../core/services/helper.service';

@Component({
    selector: 'app-manager-widgets-types-edit',
    templateUrl: './manager-widgets-types-edit.component.html',
    styleUrls: ['./manager-widgets-types-edit.component.scss']
})

export class ManagerWidgetsTypesEditComponent implements OnInit {
    public event: EventEmitter<any> = new EventEmitter();
    public onClose: Subject<boolean>;
    editManagerWidgetsTypesForm: FormGroup;
    fieldData: any;
    renderTypeData: any;
    selectboxOptions: any;
    managerWidgetTypes: ManagerWidgetsTypes;
    isFormSubmitted = false;
    isWidgetsTypeLoaded = false;
    editorOptions = {theme: 'vs-dark', language: 'html'};

    constructor(
        public translate: TranslateService,
        public bsModalRef: BsModalRef,
        private formBuilder: FormBuilder,
        private toastr: ToastrService,
        private widgetsTypesService: WidgetsTypesService,
        private  widgetsService: WidgetsService,
        private helperService: HelperService
    ) {

    }

    get managerWidgetsTypesControl() {
        return this.editManagerWidgetsTypesForm.controls;
    }

    ngOnInit() {
        this.onClose = new Subject();
        this.loadForms();
        this.getFieldAll();
    }

    getFieldAll() {
        this.widgetsTypesService.getFieldAll()
            .subscribe(
                result => {
                    this.fieldData = result['data'];
                    this.renderTypeData = result['render_type'];
                });
    }

    loadForms() {
        this.editManagerWidgetsTypesForm = this.formBuilder.group({
            id: [this.managerWidgetTypes.id],
            field_id: [this.managerWidgetTypes.field_id, Validators.required],
            widget_type_id: [this.managerWidgetTypes.widget_type_id, Validators.required],
            render_type: [this.managerWidgetTypes.render_type, Validators.required],
            label: [this.managerWidgetTypes.label, Validators.required],
            is_required: [this.managerWidgetTypes.is_required],
            select_options: this.formBuilder.array([]),
            index: [this.managerWidgetTypes.index, [Validators.required, Validators.min(0)]]
        });
        this.isWidgetsTypeLoaded = true;
        if (this.editManagerWidgetsTypesForm.get('field_id').value == 3 && this.managerWidgetTypes.select_options != []) {
            this.addDynamicOptions(this.managerWidgetTypes.select_options);
        }
    }

    addDynamicOptions(options) {
        let that = this;
        options.forEach(function (element) {
            let control = <FormArray>that.editManagerWidgetsTypesForm.controls.select_options;
            control.push(
                that.formBuilder.group({
                    value: [element.value, Validators.required],
                    label: [element.label, Validators.required],
                })
            );
        });
    }

    addOption() {
        let control = <FormArray>this.editManagerWidgetsTypesForm.controls.select_options;
        control.push(
            this.formBuilder.group({
                value: [null, Validators.required],
                label: [null, Validators.required],
            })
        );
    }

    deleteOption(index) {
        let control = <FormArray>this.editManagerWidgetsTypesForm.controls.select_options;
        control.removeAt(index);
    }

    onChange(event) {
        if (event.id == 3) {
            this.addOption();
            return;
        }

        const arr = <FormArray>this.editManagerWidgetsTypesForm.controls.select_options;
        arr.controls = [];
        arr.removeAt(0);
        arr.reset();
    }

    onSubmit() {
        this.isFormSubmitted = true;

        if (this.editManagerWidgetsTypesForm.invalid) {
            return;
        }

        this.widgetsTypesService.updateFeildInWidget(this.editManagerWidgetsTypesForm.value)
            .subscribe(
                data => {
                    this.toastr.success(this.translate.instant('settings_web.manager_widgets_types.messages.update'), this.translate.instant('settings_web.manager_widgets_types.title'));
                    this.event.emit({data: true});
                    this.onCancel();
                });
    }

    onCancel() {
        this.onClose.next(false);
        this.bsModalRef.hide();
    }
}
