import {Component, OnInit, ViewChild} from '@angular/core';
import {BsModalRef, BsModalService} from 'ngx-bootstrap/modal';
import {HttpClient} from '@angular/common/http';
import {ExportAsConfig, ExportAsService} from 'ngx-export-as';
import {ToastrService} from 'ngx-toastr';
import {TranslateService} from '@ngx-translate/core';
import {DataTableDirective} from 'angular-datatables';
import {Subject} from 'rxjs';
import Swal from 'sweetalert2';
import {ActivatedRoute, Router} from '@angular/router';

import {PagesService} from '../../../../core/services/pages.service';
import {HelperService} from '../../../../core/services/helper.service';
import {AuthenticationService} from '../../../../core/services/authentication.service';

import {ManagerWidgetsCreateComponent} from '../../components/manager-widgets-create/manager-widgets-create.component';
import {ManagerWidgetsEditComponent} from '../../components/manager-widgets-edit/manager-widgets-edit.component';

import {WidgetsCreateComponent} from '../../components/widgets-create/widgets-create.component';
import {WidgetsEditComponent} from '../../components/widgets-edit/widgets-edit.component';
import {WidgetsEditDataComponent} from '../../components/widgets-edit-data/widgets-edit-data.component';
import {WidgetsService} from '../../../../core/services/widgets.service';

import {DatatablesResponse} from '../../../../core/helpers/datatables-response.helper';
import {environment} from '../../../../../environments/environment';

import 'datatables.net';
import 'datatables.net-bs4';

declare let $: any;

@Component({
    selector: 'app-manager-widgets',
    templateUrl: './manager-widgets.component.html',
    styleUrls: ['./manager-widgets.component.scss']
})

export class ManagerWidgetsComponent implements OnInit {
    public apiUrl = environment.apiUrl;
    public modalRef: BsModalRef;
    @ViewChild(DataTableDirective, {static: true})
    dtElement: DataTableDirective;
    dtTrigger: Subject<any> = new Subject();
    formTables: any;
    params: any;
    dtOptions: any = {};
    loginUser: any;
    pageData: any = [];
    managerWidgets: any = [];
    exportAsConfig: ExportAsConfig = {
        type: 'pdf',
        elementIdOrContent: 'custom_field_table',
    };
    modalConfigs = {
        animated: true,
        keyboard: true,
        backdrop: true,
        ignoreBackdropClick: false,
        class: 'inmodal modal-dialog-centered animated fadeIn modal-lg',
        initialState: {}
    };

    constructor(
        public translate: TranslateService,
        private modalService: BsModalService,
        private http: HttpClient,
        private exportAsService: ExportAsService,
        private toastr: ToastrService,
        private authenticationService: AuthenticationService,
        private pagesService: PagesService,
        private router: Router,
        private route: ActivatedRoute,
        private widgetsService: WidgetsService,
        private helperService: HelperService
    ) {
        this.authenticationService.loginUser.subscribe(x => this.loginUser = x);
        this.route.paramMap.subscribe(params => {
            this.params = params;
        });
    }

    ngOnInit() {
        // this.getAllFormFields();
        this.loadManagerWidgetDatatable();
    }

    loadManagerWidgetDatatable() {
        let that = this;
        this.dtOptions = {
            pagingType: 'full_numbers',
            pageLength: that.loginUser.settings.tables_pagination_limit,
            serverSide: true,
            processing: true,
            dom: '<"html5buttons"B>ltfrtip',
            columns: [{
                'sortable': false,
                'target': [0]
            }, {
                'sortable': false,
                'target': [1]
            }, {
                'sortable': false,
                'target': [2]
            }, {
                'sortable': false,
                'target': [3]
            }, {
                'sortable': false,
                'width': '5%',
                'target': [4]
            }],
            buttons: [{
                extend: 'csv',
                title: this.translate.instant('settings_web.manager-widgets.title'),
                className: 'btn btn-datatable-gredient',
                action: function (e, dt, node, config) {
                    that.exportFiles('csv');
                }
            }, {
                extend: 'excel',
                title: this.translate.instant('settings_web.manager-widgets.title'),
                className: 'btn btn-datatable-gredient',
                action: function (e, dt, node, config) {
                    that.exportFiles('xlsx');
                }
            }, {
                extend: 'pdf',
                title: this.translate.instant('settings_web.manager-widgets.title'),
                className: 'btn btn-datatable-gredient',
                action: function (e, dt, node, config) {
                    that.exportFiles('pdf');
                }
            }],
            language: {
                sLengthMenu: 'Show _MENU_',
                sSearch: '',
                sSearchPlaceholder: 'Search ...'
            },
            ajax: (dataTablesParameters: any, callback) => {
                dataTablesParameters.page_id = this.params.get('id');
                this.http
                    .post<DatatablesResponse>(this.apiUrl + '/api/all-manager-widgets', dataTablesParameters, {})
                    .subscribe(resp => {
                        this.managerWidgets = resp.data;
                        this.pageData = resp.parentData;
                        callback({
                            recordsTotal: resp.recordsTotal,
                            recordsFiltered: resp.recordsFiltered,
                            data: [],
                        });
                    });
            }
        };
    }

    exportFiles(type) {
        this.exportAsConfig.type = type;
        this.exportAsService.save(this.exportAsConfig, 'managerWidget').subscribe(() => {
        });
    }

    ngAfterViewInit(): void {
        this.dtTrigger.next();
    }

    ngOnDestroy(): void {
        this.dtTrigger.unsubscribe();
    }

    rerender(): void {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
            dtInstance.destroy();

            setTimeout(() => {
                this.dtTrigger.next();

                if (this.managerWidgets.length > 0) {
                    $('.tfoot_dt').addClass('d-none');
                } else {
                    $('.tfoot_dt').removeClass('d-none');
                }
            });
        });
    }

    openManagerWidgetCreateModal(regions_id, regions_number) {
        let modalConfig = {
            animated: true,
            keyboard: true,
            backdrop: true,
            ignoreBackdropClick: false,
            class: 'inmodal modal-dialog-centered animated fadeIn modal-lg',
            initialState: {
                managerWidgets: {
                    pages_id: this.params.get('id'),
                    regions_id: regions_id,
                    regions_number: regions_number
                }
            }
        };
        this.modalRef = this.modalService.show(ManagerWidgetsCreateComponent, modalConfig);
        this.modalRef.content.event.subscribe(data => {
            this.rerender();
        });
    }

    openManagerWidgetEditModal(ManagerWidgets) {
        this.pagesService.getWidgetById(ManagerWidgets.id)
            .subscribe(
                resp => {
                    let modalConfig = {
                        animated: true,
                        keyboard: true,
                        backdrop: true,
                        ignoreBackdropClick: false,
                        class: 'inmodal modal-dialog-centered animated fadeIn modal-lg',
                        initialState: {
                            managerWidgets: resp
                        }
                    };
                    this.modalRef = this.modalService.show(ManagerWidgetsEditComponent, modalConfig);
                    this.modalRef.content.event.subscribe(data => {
                        this.rerender();
                    });
                });
    }

    removeManagerWidget(id) {
        Swal.fire({
            title: this.translate.instant('common.swal.title'),
            text: this.translate.instant('common.swal.text') + ' ' + this.translate.instant('settings_web.manager_widgets.title'),
            type: 'warning',
            showCancelButton: true,
            confirmButtonText: this.translate.instant('common.swal.confirmButtonText'),
            cancelButtonText: this.translate.instant('common.swal.cancelButtonText')
        }).then((result) => {
            if (result.value) {
                this.pagesService.deleteManagerWidget(id)
                    .subscribe(
                        data => {
                            this.toastr.success(this.translate.instant('settings_web.manager_widgets.messages.delete'), this.translate.instant('settings_web.manager_widgets.title'));
                            this.rerender();
                        });
            }
        });
    }

    openWidgetCreateModal() {
        this.modalRef = this.modalService.show(WidgetsCreateComponent, this.modalConfigs);
        this.modalRef.content.event.subscribe(data => {
            this.rerender();
        });
    }

    openWidgetEditModal(Widgets) {
        this.widgetsService.getById(Widgets.id)
            .subscribe(
                resp => {
                    let modalConfig = {
                        animated: true,
                        keyboard: true,
                        backdrop: true,
                        ignoreBackdropClick: false,
                        class: 'inmodal modal-dialog-centered animated fadeIn modal-lg',
                        initialState: {
                            widgets: resp['data']
                        }
                    };
                    this.modalRef = this.modalService.show(WidgetsEditComponent, modalConfig);
                    this.modalRef.content.event.subscribe(data => {
                        this.rerender();
                    });
                });
    }

    openWidgetEditDataModal(Widgets) {
        this.widgetsService.getById(Widgets.widgets_id)
            .subscribe(
                resp => {
                    let modalConfig = {
                        animated: true,
                        keyboard: true,
                        backdrop: true,
                        ignoreBackdropClick: false,
                        class: 'inmodal modal-dialog-centered animated fadeIn modal-lg',
                        initialState: {
                            widgets: resp['data'],
                            customFields: resp['customFields']['single'],
                            customFieldsMultiple: resp['customFields']['multiple']
                        }
                    };
                    this.modalRef = this.modalService.show(WidgetsEditDataComponent, modalConfig);
                    this.modalRef.content.event.subscribe(data => {
                        this.rerender();
                    });
                });
    }

    changeStatus(event, id) {
        this.pagesService.changeStatus({id: id, status: event})
            .subscribe(
                data => {
                    this.toastr.success(this.translate.instant('settings_web.manager_regions.messages.status'), this.translate.instant('settings_web.manager_regions.title'));
                    this.rerender();
                });
    }

}

