import {Component, EventEmitter, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {BsModalRef} from 'ngx-bootstrap/modal';
import {ToastrService} from 'ngx-toastr';
import {TranslateService} from '@ngx-translate/core';
import {Subject} from 'rxjs';

import {ManagerRegions} from '../../../../shared/models/manager-regions.model';
import {TemplatesService} from '../../../../core/services/templates.service';
import {RegionsService} from '../../../../core/services/regions.service';
import {HelperService} from '../../../../core/services/helper.service';

@Component({
    selector: 'app-manager-regions-create',
    templateUrl: './manager-regions-create.component.html',
    styleUrls: ['./manager-regions-create.component.scss']
})

export class ManagerRegionsCreateComponent implements OnInit {
    public event: EventEmitter<any> = new EventEmitter();
    public onClose: Subject<boolean>;
    regionData: any;
    selectboxOptions: any;
    managerRegions: ManagerRegions;
    createManagerRegionsForm: FormGroup;
    isFormSubmitted = false;
    isPageLoaded = false;
    editorOptions = {theme: 'vs-dark', language: 'html'};

    constructor(
        public translate: TranslateService,
        public bsModalRef: BsModalRef,
        private formBuilder: FormBuilder,
        private toastr: ToastrService,
        private templatesService: TemplatesService,
        private  regionsService: RegionsService,
        private helperService: HelperService
    ) {
    }

    get managerRegionsControl() {
        return this.createManagerRegionsForm.controls;
    }

    ngOnInit() {
        this.onClose = new Subject();
        this.loadForms();
        this.getAllRegions();
    }

    getAllRegions() {
        this.regionsService.getAll()
            .subscribe(
                data => {
                    this.regionData = data;
                });
    }

    loadForms() {
        this.createManagerRegionsForm = this.formBuilder.group({
            regions_id: [null, Validators.required],
            index: [null, [Validators.required, Validators.min(0)]],
        });
    }

    onSubmit() {
        this.isFormSubmitted = true;

        if (this.createManagerRegionsForm.invalid) {
            return;
        }
        this.createManagerRegionsForm.value.templates_id = this.managerRegions.templates_id;
        this.templatesService.managerRegions(this.createManagerRegionsForm.value)
            .subscribe(
                data => {
                    this.toastr.success(this.translate.instant('settings_web.manager_regions.messages.create'), this.translate.instant('settings_web.manager_regions.title'));
                    this.event.emit({data: true});
                    this.onCancel();
                });
    }

    onCancel() {
        this.onClose.next(false);
        this.bsModalRef.hide();
    }
}
