import {Component, OnInit, ViewChild, ViewEncapsulation} from '@angular/core';
import {TabsetComponent} from 'ngx-bootstrap/tabs';
import {ActivatedRoute} from '@angular/router';

import {SettingService} from '../../../../core/services/setting.service';
import {HelperService} from '../../../../core/services/helper.service';
import {AuthenticationService} from 'src/app/core/services/authentication.service';

@Component({
    selector: 'app-settings',
    templateUrl: './settings-web.component.html',
    styleUrls: ['./settings-web.component.scss'],
    encapsulation: ViewEncapsulation.None
})

export class SettingsWebComponent implements OnInit {
    @ViewChild('tabset', {static: true}) tabset: TabsetComponent;
    countries: any;
    loginUser: any;
    activeSettingTab = '1';
    isSettingsLoad = false;
    settings: any = [];

    constructor(
        private settingService: SettingService,
        private helperService: HelperService,
        private authenticationService: AuthenticationService,
        private route: ActivatedRoute
    ) {
        // this.route.queryParams.subscribe(params => {
        //     if( params['tab'] && Number(params['tab'])){
        //         this.activeSettingTab = params['tab'];
        //     }
        // });
        this.authenticationService.loginUser.subscribe(x => this.loginUser = x);
    }

    ngOnInit() {
    }

    setSettingTab($event) {
        this.activeSettingTab = $event.id;
    }

    getActiveSettingTab(tab) {
        return this.activeSettingTab === tab;
    }
}
