import {Component, OnInit, ViewChild} from '@angular/core';
import {BsModalRef, BsModalService} from 'ngx-bootstrap/modal';
import {HttpClient} from '@angular/common/http';
import {ExportAsConfig, ExportAsService} from 'ngx-export-as';
import {ToastrService} from 'ngx-toastr';
import {TranslateService} from '@ngx-translate/core';
import {DataTableDirective} from 'angular-datatables';
import {Subject} from 'rxjs';
import Swal from 'sweetalert2';

import {RegionsService} from '../../../../core/services/regions.service';
import {HelperService} from '../../../../core/services/helper.service';
import {AuthenticationService} from '../../../../core/services/authentication.service';

import {RegionsCreateComponent} from '../regions-create/regions-create.component';
import {RegionsEditComponent} from '../regions-edit/regions-edit.component';

import {DatatablesResponse} from '../../../../core/helpers/datatables-response.helper';
import {environment} from '../../../../../environments/environment';

import 'datatables.net';
import 'datatables.net-bs4';

declare let $: any;

@Component({
    selector: 'app-regions',
    templateUrl: './regions.component.html',
    styleUrls: ['./regions.component.scss']
})

export class RegionsComponent implements OnInit {
    public modalRef: BsModalRef;
    @ViewChild(DataTableDirective, {static: true})
    dtElement: DataTableDirective;
    dtTrigger: Subject<any> = new Subject();
    formTables: any;
    dtOptions: any = {};
    loginUser: any;
    regions: any = [];
    exportAsConfig: ExportAsConfig = {
        type: 'pdf',
        elementIdOrContent: 'custom_field_table',
    };
    modalConfigs = {
        animated: true,
        keyboard: true,
        backdrop: true,
        ignoreBackdropClick: false,
        class: 'inmodal modal-dialog-centered animated fadeIn modal-lg'
    };
    private apiUrl = environment.apiUrl;

    constructor(
        public translate: TranslateService,
        private modalService: BsModalService,
        private http: HttpClient,
        private exportAsService: ExportAsService,
        private toastr: ToastrService,
        private authenticationService: AuthenticationService,
        private regionsService: RegionsService,
        private helperService: HelperService
    ) {
        this.authenticationService.loginUser.subscribe(x => this.loginUser = x);
    }

    ngOnInit() {
        // this.getAllFormFields();
        this.loadRegionDatatable();
    }

    loadRegionDatatable() {
        let that = this;
        this.dtOptions = {
            pagingType: 'full_numbers',
            pageLength: that.loginUser.settings.tables_pagination_limit,
            serverSide: true,
            processing: true,
            dom: '<"html5buttons"B>ltfrtip',
            columns: [{
                'sortable': true,
                'width': '2%',
                'target': [0]
            }, {
                'sortable': true,
                'target': [1]
            }, {
                'sortable': true,
                'target': [2]
            }, {
                'sortable': false,
                'target': [3]
            }, {
                'sortable': false,
                'width': '5%',
                'target': [4]
            }],
            buttons: [{
                extend: 'csv',
                title: this.translate.instant('settings_web.regions.title'),
                className: 'btn btn-datatable-gredient',
                action: function (e, dt, node, config) {
                    that.exportFiles('csv');
                }
            }, {
                extend: 'excel',
                title: this.translate.instant('settings_web.regions.title'),
                className: 'btn btn-datatable-gredient',
                action: function (e, dt, node, config) {
                    that.exportFiles('xlsx');
                }
            }, {
                extend: 'pdf',
                title: this.translate.instant('settings_web.regions.title'),
                className: 'btn btn-datatable-gredient',
                action: function (e, dt, node, config) {
                    that.exportFiles('pdf');
                }
            }],
            language: {
                sLengthMenu: 'Show _MENU_',
                sSearch: '',
                sSearchPlaceholder: 'Search ...'
            },
            ajax: (dataTablesParameters: any, callback) => {
                this.http
                    .post<DatatablesResponse>(this.apiUrl + '/api/all-regions', dataTablesParameters, {})
                    .subscribe(resp => {
                        this.regions = resp.data;
                        callback({
                            recordsTotal: resp.recordsTotal,
                            recordsFiltered: resp.recordsFiltered,
                            data: [],
                        });
                    });
            }
        };
    }

    exportFiles(type) {
        this.exportAsConfig.type = type;
        this.exportAsService.save(this.exportAsConfig, 'region').subscribe(() => {
        });
    }

    ngAfterViewInit(): void {
        this.dtTrigger.next();
    }

    ngOnDestroy(): void {
        this.dtTrigger.unsubscribe();
    }

    rerender(): void {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
            dtInstance.destroy();

            setTimeout(() => {
                this.dtTrigger.next();

                if (this.regions.length > 0) {
                    $('.tfoot_dt').addClass('d-none');
                } else {
                    $('.tfoot_dt').removeClass('d-none');
                }
            });
        });
    }

    openRegionCreateModal() {
        this.modalRef = this.modalService.show(RegionsCreateComponent, this.modalConfigs);
        this.modalRef.content.event.subscribe(data => {
            this.rerender();
        });
    }

    openRegionEditModal(Regions) {
        this.regionsService.getById(Regions.id)
            .subscribe(
                resp => {
                    let modalConfig = {
                        animated: true,
                        keyboard: true,
                        backdrop: true,
                        ignoreBackdropClick: false,
                        class: 'inmodal modal-dialog-centered animated fadeIn modal-lg',
                        initialState: {
                            regions: resp
                        }
                    };
                    this.modalRef = this.modalService.show(RegionsEditComponent, modalConfig);
                    this.modalRef.content.event.subscribe(data => {
                        this.rerender();
                    });
                });
    }

    removeRegion(id) {
        Swal.fire({
            title: this.translate.instant('common.swal.title'),
            text: this.translate.instant('common.swal.text') + ' ' + this.translate.instant('settings_web.regions.title1'),
            type: 'warning',
            showCancelButton: true,
            confirmButtonText: this.translate.instant('common.swal.confirmButtonText'),
            cancelButtonText: this.translate.instant('common.swal.cancelButtonText')
        }).then((result) => {
            if (result.value) {
                this.regionsService.delete(id)
                    .subscribe(
                        data => {
                            this.toastr.success(this.translate.instant('settings_web.regions.messages.delete'), this.translate.instant('settings_web.regions.title'));
                            this.rerender();
                        });
            }
        });
    }

}

