import {Component, EventEmitter, OnInit} from '@angular/core';
import {FormArray, FormBuilder, FormGroup, Validators} from '@angular/forms';
import {BsModalRef} from 'ngx-bootstrap/modal';
import {ToastrService} from 'ngx-toastr';
import {TranslateService} from '@ngx-translate/core';
import {Subject} from 'rxjs';

import {Widgets} from '../../../../shared/models/widgets.model';

import {WidgetsService} from '../../../../core/services/widgets.service';
import {WidgetsTypesService} from '../../../../core/services/widgets-types.service';
import {HelperService} from '../../../../core/services/helper.service';

@Component({
    selector: 'app-widgets-edit-data',
    templateUrl: './widgets-edit-data.component.html',
    styleUrls: ['./widgets-edit-data.component.scss']
})

export class WidgetsEditDataComponent implements OnInit {
    public event: EventEmitter<any> = new EventEmitter();
    public onClose: Subject<boolean>;
    editWidgetsForm: FormGroup;
    formTables: any;
    selectboxOptions: any;
    widgets: Widgets;
    customFields: any;
    customFieldsMultiple: any;
    data_hint:string;
    AllDataWidget: any;
    useData: any = [];
    widgetsTypesData: any;
    isFormSubmitted = false;
    isPageLoaded = false;
    leftEcho = '{{';
    rightEcho = '}}';
    data_hint_multiple: string;
    editorOptions = {theme: 'vs-dark', language: 'html'};

    constructor(
        public translate: TranslateService,
        public bsModalRef: BsModalRef,
        private formBuilder: FormBuilder,
        private toastr: ToastrService,
        private widgetsService: WidgetsService,
        private helperService: HelperService,
        private widgetsTypesService: WidgetsTypesService
    ) {

    }

    get widgetsControl() {
        return this.editWidgetsForm.controls;
    }

    ngOnInit() {
        let that = this;
        this.onClose = new Subject();
        this.data_hint_multiple = "@foreach($" + this.widgets.machine_name + "['multipleData'] as $value)<br>";
        that.customFieldsMultiple.forEach(function (customFieldsMultiple) {
            if(customFieldsMultiple.field_type == 'file'){
                that.data_hint_multiple = that.data_hint_multiple + "{{$file_path}}";
            }
            that.data_hint_multiple = that.data_hint_multiple + "{{$value['"+customFieldsMultiple.field_column+"']}}<br>";
        });
        that.data_hint_multiple = that.data_hint_multiple + "@endforeach";
        this.getUseData();
        this.getAllWidgetsTypes();
        this.loadForms();
    }

    getAllWidgetsTypes() {
        this.widgetsTypesService.getAll()
            .subscribe(
                data => {
                    this.widgetsTypesData = data;
                });
    }
    getUseData() {
        this.widgetsService.getUseData()
            .subscribe(
                data => {
                    this.AllDataWidget = data;
                    if(this.widgets.data_type){
                        for (var i = 0; i < this.AllDataWidget.length; ++i) {
                            if(this.AllDataWidget[i].id == this.widgets.data_type){
                                this.useDataChange(this.AllDataWidget[i], 'load');
                            }
                        }
                    }
                });
    }

    useDataChange(data, action){
        if(action != 'load'){
            this.editWidgetsForm.controls['data_id'].setValue([[]]);
        }
        if(data && data.data){
            this.useData = data.data;
            this.editWidgetsForm.get('data_id').setValidators([Validators.required]);
            this.editWidgetsForm.get('data_id').updateValueAndValidity();
            this.data_hint = data.data_hint;
        } else {
            this.editWidgetsForm.get('data_id').setValidators([]);
            this.editWidgetsForm.get('data_id').updateValueAndValidity();
            this.data_hint = '';
        }
    }

    loadForms() {
        this.editWidgetsForm = this.formBuilder.group({
            id: [this.widgets.id],
            name: [this.widgets.name, Validators.required],
            description: [this.widgets.description],
            widget_type_id: [{value:this.widgets.widget_type_id, disabled: true}, Validators.required],
            content_html: [this.widgets.content_html, Validators.required],
            data_type:[this.widgets.data_type],
            data_id:[this.widgets.data_id],
            custom_field: this.formBuilder.array([]),
            custom_field_multiple: this.formBuilder.array([])
        });
        this.isPageLoaded = true;
        if (this.widgets.custom_field_multiple && this.widgets.custom_field_multiple.length > 0) {
            this.addDynamicOptions('custom_field_multiple', this.widgets.custom_field_multiple, this.customFieldsMultiple);
        } else {
            this.addOption('custom_field_multiple', this.customFieldsMultiple);
        }
        if (this.widgets.custom_field && this.widgets.custom_field.length > 0) {
            this.addDynamicOptions('custom_field', this.widgets.custom_field, this.customFields);
        } else {
            this.addOption('custom_field', this.customFields);
        }
    }

    addOption(key, dataBuildField) {
        let control = <FormArray>this.editWidgetsForm.controls[key];
        let dataBuild = [];
        dataBuildField.forEach(function (element) {
            if (element.is_required == 1) {
                dataBuild[element.field_column] = [null, Validators.required];
            } else {
                dataBuild[element.field_column] = [null];
            }

        });
        control.push(
            this.formBuilder.group(dataBuild)
        );
    }

    dropzoneCallback(data) {
        let control = (<FormArray>this.editWidgetsForm.get(data.parentFormArrayName)).at(data.parentFormGroupName);
        control.patchValue({
            [data.parentFormControlName]: data.file
        });
    }

    deleteOption(index) {
        let control = <FormArray>this.editWidgetsForm.controls.custom_field_multiple;
        control.removeAt(index);
    }

    addDynamicOptions(key, options, dataBuildField) {
        let that = this;
        options.forEach(function (elementParent) {
            let dataBuild = [];
            let control = <FormArray>that.editWidgetsForm.controls[key];
            dataBuildField.forEach(function (element) {
                if (element.is_required == 1) {
                    dataBuild[element.field_column] = [elementParent[element.field_column], Validators.required];
                } else {
                    dataBuild[element.field_column] = [elementParent[element.field_column]];
                }
            });
            control.push(
                that.formBuilder.group(dataBuild)
            );
        });
    }

    onSubmit() {
        this.isFormSubmitted = true;

        if (this.editWidgetsForm.invalid) {
            return;
        }

        this.widgetsService.update(this.editWidgetsForm.value)
            .subscribe(
                data => {
                    this.toastr.success(this.translate.instant('settings_web.widgets.messages.update'), this.translate.instant('settings_web.widgets.title'));
                    this.event.emit({data: true});
                    this.onCancel();
                });
    }

    onCancel() {
        this.onClose.next(false);
        this.bsModalRef.hide();
    }
}
