import {Component, EventEmitter, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {BsModalRef} from 'ngx-bootstrap/modal';
import {ToastrService} from 'ngx-toastr';
import {TranslateService} from '@ngx-translate/core';
import {Subject} from 'rxjs';

import {ManagerWidgets} from '../../../../shared/models/manager-widgets.model';

import {PagesService} from '../../../../core/services/pages.service';
import {WidgetsService} from '../../../../core/services/widgets.service';
import {HelperService} from '../../../../core/services/helper.service';

@Component({
    selector: 'app-manager-widgets-edit',
    templateUrl: './manager-widgets-edit.component.html',
    styleUrls: ['./manager-widgets-edit.component.scss']
})

export class ManagerWidgetsEditComponent implements OnInit {
    public event: EventEmitter<any> = new EventEmitter();
    public onClose: Subject<boolean>;
    editManagerWidgetsForm: FormGroup;
    widgetData: any;
    selectboxOptions: any;
    managerWidgets: ManagerWidgets;
    isFormSubmitted = false;
    isPageLoaded = false;
    editorOptions = {theme: 'vs-dark', language: 'html'};

    constructor(
        public translate: TranslateService,
        public bsModalRef: BsModalRef,
        private formBuilder: FormBuilder,
        private toastr: ToastrService,
        private pagesService: PagesService,
        private  widgetsService: WidgetsService,
        private helperService: HelperService
    ) {

    }

    get managerWidgetsControl() {
        return this.editManagerWidgetsForm.controls;
    }

    ngOnInit() {
        this.onClose = new Subject();
        this.loadForms();
        this.getAllWidgets();
    }

    getAllWidgets() {
        this.widgetsService.getAll()
            .subscribe(
                data => {
                    this.widgetData = data;
                });
    }

    loadForms() {
        this.editManagerWidgetsForm = this.formBuilder.group({
            id: [this.managerWidgets.id],
            widgets_id: [this.managerWidgets.widgets_id, Validators.required],
            regions_id: [this.managerWidgets.regions_id, Validators.required],
            pages_id: [this.managerWidgets.pages_id, Validators.required],
            index: [this.managerWidgets.index, Validators.required],
        });
        this.isPageLoaded = true;

    }

    onSubmit() {
        this.isFormSubmitted = true;

        if (this.editManagerWidgetsForm.invalid) {
            return;
        }

        this.pagesService.updateManagerWidget(this.editManagerWidgetsForm.value)
            .subscribe(
                data => {
                    this.toastr.success(this.translate.instant('settings_web.manager_widgets.messages.update'), this.translate.instant('settings_web.manager_widgets.title'));
                    this.event.emit({data: true});
                    this.onCancel();
                });
    }

    onCancel() {
        this.onClose.next(false);
        this.bsModalRef.hide();
    }
}
