import {Component, EventEmitter, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {BsModalRef} from 'ngx-bootstrap/modal';
import {ToastrService} from 'ngx-toastr';
import {TranslateService} from '@ngx-translate/core';
import {Subject} from 'rxjs';

import {WidgetsTypes} from '../../../../shared/models/widgets-types.model';

import {WidgetsTypesService} from '../../../../core/services/widgets-types.service';
import {TemplatesService} from '../../../../core/services/templates.service';
import {HelperService} from '../../../../core/services/helper.service';

@Component({
    selector: 'app-widgets-types-edit',
    templateUrl: './widgets-types-edit.component.html',
    styleUrls: ['./widgets-types-edit.component.scss']
})

export class WidgetsTypesEditComponent implements OnInit {
    public event: EventEmitter<any> = new EventEmitter();
    public onClose: Subject<boolean>;
    editWidgetsTypesForm: FormGroup;
    formTables: any;
    selectboxOptions: any;
    templateData: any;
    widgets_types: WidgetsTypes;
    isFormSubmitted = false;
    isWidgetsTypeLoaded = false;
    editorOptions = {theme: 'vs-dark', language: 'html'};

    constructor(
        public translate: TranslateService,
        public bsModalRef: BsModalRef,
        private formBuilder: FormBuilder,
        private toastr: ToastrService,
        private widgets_typesService: WidgetsTypesService,
        private helperService: HelperService,
        private templatesService: TemplatesService
    ) {

    }

    get widgetsTypesControl() {
        return this.editWidgetsTypesForm.controls;
    }

    ngOnInit() {
        this.onClose = new Subject();
        this.getAllTemplates();
        this.loadForms();
    }

    getAllTemplates() {
        this.templatesService.getAll()
            .subscribe(
                data => {
                    this.templateData = data;
                });
    }

    loadForms() {
        this.editWidgetsTypesForm = this.formBuilder.group({
            id: [this.widgets_types.id],
            name: [this.widgets_types.name, Validators.required],
            description: [this.widgets_types.description],
        });
        this.isWidgetsTypeLoaded = true;

    }

    onSubmit() {
        this.isFormSubmitted = true;

        if (this.editWidgetsTypesForm.invalid) {
            return;
        }

        this.widgets_typesService.update(this.editWidgetsTypesForm.value)
            .subscribe(
                data => {
                    this.toastr.success(this.translate.instant('settings_web.widgets_types.messages.update'), this.translate.instant('settings_web.widgets_types.title'));
                    this.event.emit({data: true});
                    this.onCancel();
                });
    }

    onCancel() {
        this.onClose.next(false);
        this.bsModalRef.hide();
    }
}
