import {Component, EventEmitter, OnInit} from '@angular/core';
import {FormArray, FormBuilder, FormGroup, Validators} from '@angular/forms';
import {BsModalRef} from 'ngx-bootstrap/modal';
import {ToastrService} from 'ngx-toastr';
import {TranslateService} from '@ngx-translate/core';
import {Subject} from 'rxjs';

import {ManagerWidgetsTypes} from '../../../../shared/models/manager-widgets-types.model';
import {WidgetsTypesService} from '../../../../core/services/widgets-types.service';
import {HelperService} from '../../../../core/services/helper.service';

@Component({
    selector: 'app-manager-widgets-types-create',
    templateUrl: './manager-widgets-types-create.component.html',
    styleUrls: ['./manager-widgets-types-create.component.scss']
})

export class ManagerWidgetsTypesCreateComponent implements OnInit {
    public event: EventEmitter<any> = new EventEmitter();
    public onClose: Subject<boolean>;
    fieldData: any;
    renderTypeData: any;
    selectboxOptions: any;
    managerWidgetsTypes: ManagerWidgetsTypes;
    createManagerWidgetsTypesForm: FormGroup;
    isFormSubmitted = false;
    isWidgetsTypeLoaded = false;
    editorOptions = {theme: 'vs-dark', language: 'html'};

    constructor(
        public translate: TranslateService,
        public bsModalRef: BsModalRef,
        private formBuilder: FormBuilder,
        private toastr: ToastrService,
        // private widgetsTypesService: WidgetsTypesService,
        private  widgetsTypesService: WidgetsTypesService,
        private helperService: HelperService
    ) {
    }

    get managerWidgetsTypesControl() {
        return this.createManagerWidgetsTypesForm.controls;
    }

    ngOnInit() {
        this.onClose = new Subject();
        this.loadForms();
        this.getFieldAll();
    }

    getFieldAll() {
        this.widgetsTypesService.getFieldAll()
            .subscribe(
                result => {
                    this.fieldData = result['data'];
                    this.renderTypeData = result['render_type'];
                });
    }

    loadForms() {
        this.createManagerWidgetsTypesForm = this.formBuilder.group({
            field_id: [null, Validators.required],
            label: [null, Validators.required],
            is_required: [null],
            render_type: [null, Validators.required],
            select_options: this.formBuilder.array([]),
            index: [null, [Validators.required, Validators.min(0)]],
        });
    }

    addOption() {
        let control = <FormArray>this.createManagerWidgetsTypesForm.controls.select_options;
        control.push(
            this.formBuilder.group({
                value: [null, Validators.required],
                label: [null, Validators.required],
            })
        );
    }

    deleteOption(index) {
        let control = <FormArray>this.createManagerWidgetsTypesForm.controls.select_options;
        control.removeAt(index);
    }

    onChange(event) {
        if (event.id == 3) {
            this.addOption();
            return;
        }

        const arr = <FormArray>this.createManagerWidgetsTypesForm.controls.select_options;
        arr.controls = [];
        arr.removeAt(0);
        arr.reset();
    }

    onSubmit() {
        this.isFormSubmitted = true;
        if (this.createManagerWidgetsTypesForm.invalid) {
            return;
        }
        if (this.createManagerWidgetsTypesForm.value.field_type == 3) {
            this.createManagerWidgetsTypesForm.patchValue({default_value: this.createManagerWidgetsTypesForm.value.select_options});
        }
        this.createManagerWidgetsTypesForm.value.widget_type_id = this.managerWidgetsTypes.widget_type_id;
        this.widgetsTypesService.addFeildInWidget(this.createManagerWidgetsTypesForm.value)
            .subscribe(
                data => {
                    this.toastr.success(this.translate.instant('settings_web.manager_widgets_types.messages.create'), this.translate.instant('settings_web.manager_widgets_types.title'));
                    this.event.emit({data: true});
                    this.onCancel();
                });
    }

    onCancel() {
        this.onClose.next(false);
        this.bsModalRef.hide();
    }
}
