import {Component, EventEmitter, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {BsModalRef} from 'ngx-bootstrap/modal';
import {ToastrService} from 'ngx-toastr';
import {TranslateService} from '@ngx-translate/core';
import {Subject} from 'rxjs';

import {Themes} from '../../../../shared/models/themes.model';

import {ThemesService} from '../../../../core/services/themes.service';
import {HelperService} from '../../../../core/services/helper.service';

@Component({
    selector: 'app-themes-edit',
    templateUrl: './themes-edit.component.html',
    styleUrls: ['./themes-edit.component.scss']
})

export class ThemesEditComponent implements OnInit {
    public event: EventEmitter<any> = new EventEmitter();
    public onClose: Subject<boolean>;
    editThemesForm: FormGroup;
    formTables: any;
    selectboxOptions: any;
    themes: Themes;
    isFormSubmitted = false;
    isPageLoaded = false;
    editorOptions = {theme: 'vs-dark', language: 'html'};

    constructor(
        public translate: TranslateService,
        public bsModalRef: BsModalRef,
        private formBuilder: FormBuilder,
        private toastr: ToastrService,
        private themesService: ThemesService,
        private helperService: HelperService
    ) {

    }

    get themesControl() {
        return this.editThemesForm.controls;
    }

    ngOnInit() {
        this.onClose = new Subject();
        this.loadForms();
    }

    loadForms() {
        this.editThemesForm = this.formBuilder.group({
            id: [this.themes.id],
            name: [this.themes.name, Validators.required],
            description: [this.themes.description],
            folder: [this.themes.folder, [Validators.required, Validators.pattern(/^[a-z0-9]*$/)]],
            content_html: [this.themes.content_html, Validators.required],
        });
        this.isPageLoaded = true;

    }

    onSubmit() {
        this.isFormSubmitted = true;

        if (this.editThemesForm.invalid) {
            return;
        }

        this.themesService.update(this.editThemesForm.value)
            .subscribe(
                data => {
                    this.toastr.success(this.translate.instant('settings_web.themes.messages.update'), this.translate.instant('settings_web.themes.title'));
                    this.event.emit({data: true});
                    this.onCancel();
                });
    }

    onCancel() {
        this.onClose.next(false);
        this.bsModalRef.hide();
    }
}
