import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';

import {SettingsWebComponent} from './pages/settings-web/settings-web.component';
import {ManagerRegionsComponent} from './pages/manager-regions/manager-regions.component';
import {ManagerWidgetsComponent} from './pages/manager-widgets/manager-widgets.component';
import {ManagerWidgetTypesComponent} from './pages/manager-widgets-types/manager-widgets-types.component';
import {ManagerFileComponent} from './pages/manager-file/manager-file.component';

const routes: Routes = [
    {
        path: '',
        children: [
            {
                path: '',
                component: SettingsWebComponent,
            },
            {
                path: 'manager-regions/:id',
                component: ManagerRegionsComponent,
                data: {
                    breadcrumbs: {
                        text: 'common.managerRegions',
                        icon: 'fa fa-product-hunt',
                        hasParams: false,
                        show: false,
                        isHome: false
                    }
                }
            },
            {
                path: 'manager-widgets/:id',
                component: ManagerWidgetsComponent,
                data: {
                    breadcrumbs: {
                        text: 'common.managerWidgets',
                        icon: 'fa fa-product-hunt',
                        hasParams: false,
                        show: false,
                        isHome: false
                    }
                }
            },
            {
                path: 'manager-widgets-types/:id',
                component: ManagerWidgetTypesComponent,
                data: {
                    breadcrumbs: {
                        text: 'common.managerFields',
                        icon: 'fa fa-product-hunt',
                        hasParams: false,
                        show: false,
                        isHome: false
                    }
                }
            },
            {
                path: 'manager-file/:id',
                component: ManagerFileComponent,
                data: {
                    breadcrumbs: {
                        text: 'common.managerFields',
                        icon: 'fa fa-product-hunt',
                        hasParams: false,
                        show: false,
                        isHome: false
                    }
                }
            }
        ]
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})

export class SettingsWebRoutingModule {
}
