import {Component, EventEmitter, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {BsModalRef} from 'ngx-bootstrap/modal';
import {ToastrService} from 'ngx-toastr';
import {TranslateService} from '@ngx-translate/core';
import {Subject} from 'rxjs';

import {ManagerRegions} from '../../../../shared/models/manager-regions.model';

import {TemplatesService} from '../../../../core/services/templates.service';
import {RegionsService} from '../../../../core/services/regions.service';
import {HelperService} from '../../../../core/services/helper.service';

@Component({
    selector: 'app-manager-regions-edit',
    templateUrl: './manager-regions-edit.component.html',
    styleUrls: ['./manager-regions-edit.component.scss']
})

export class ManagerRegionsEditComponent implements OnInit {
    public event: EventEmitter<any> = new EventEmitter();
    public onClose: Subject<boolean>;
    editManagerRegionsForm: FormGroup;
    regionData: any;
    selectboxOptions: any;
    managerRegions: ManagerRegions;
    isFormSubmitted = false;
    isPageLoaded = false;
    editorOptions = {theme: 'vs-dark', language: 'html'};

    constructor(
        public translate: TranslateService,
        public bsModalRef: BsModalRef,
        private formBuilder: FormBuilder,
        private toastr: ToastrService,
        private templatesService: TemplatesService,
        private  regionsService: RegionsService,
        private helperService: HelperService
    ) {

    }

    get managerRegionsControl() {
        return this.editManagerRegionsForm.controls;
    }

    ngOnInit() {
        this.onClose = new Subject();
        this.loadForms();
        this.getAllRegions();
    }

    getAllRegions() {
        this.regionsService.getAll()
            .subscribe(
                data => {
                    this.regionData = data;
                });
    }

    loadForms() {
        this.editManagerRegionsForm = this.formBuilder.group({
            id: [this.managerRegions.id],
            regions_id: [this.managerRegions.regions_id, Validators.required],
            templates_id: [this.managerRegions.templates_id, Validators.required],
            index: [this.managerRegions.index, [Validators.required, Validators.min(0)]],
        });
        this.isPageLoaded = true;

    }

    onSubmit() {
        this.isFormSubmitted = true;

        if (this.editManagerRegionsForm.invalid) {
            return;
        }

        this.templatesService.updateManagerRegion(this.editManagerRegionsForm.value)
            .subscribe(
                data => {
                    this.toastr.success(this.translate.instant('settings_web.manager_regions.messages.update'), this.translate.instant('settings_web.manager_regions.title'));
                    this.event.emit({data: true});
                    this.onCancel();
                });
    }

    onCancel() {
        this.onClose.next(false);
        this.bsModalRef.hide();
    }
}
