import {Component, EventEmitter, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {BsModalRef} from 'ngx-bootstrap/modal';
import {ToastrService} from 'ngx-toastr';
import {TranslateService} from '@ngx-translate/core';
import {Subject} from 'rxjs';

import {Templates} from '../../../../shared/models/templates.model';

import {TemplatesService} from '../../../../core/services/templates.service';
import {HelperService} from '../../../../core/services/helper.service';

@Component({
    selector: 'app-templates-edit',
    templateUrl: './templates-edit.component.html',
    styleUrls: ['./templates-edit.component.scss']
})

export class TemplatesEditComponent implements OnInit {
    public event: EventEmitter<any> = new EventEmitter();
    public onClose: Subject<boolean>;
    editTemplatesForm: FormGroup;
    formTables: any;
    selectboxOptions: any;
    templates: Templates;
    isFormSubmitted = false;
    isPageLoaded = false;
    editorOptions = {theme: 'vs-dark', language: 'html'};

    constructor(
        public translate: TranslateService,
        public bsModalRef: BsModalRef,
        private formBuilder: FormBuilder,
        private toastr: ToastrService,
        private templatesService: TemplatesService,
        private helperService: HelperService
    ) {

    }

    get templatesControl() {
        return this.editTemplatesForm.controls;
    }

    ngOnInit() {
        this.onClose = new Subject();
        this.loadForms();
    }

    loadForms() {
        this.editTemplatesForm = this.formBuilder.group({
            id: [this.templates.id],
            name: [this.templates.name, Validators.required],
            description: [this.templates.description],
            // content_html: [this.templates.content_html, Validators.required],
        });
        this.isPageLoaded = true;

    }

    onSubmit() {
        this.isFormSubmitted = true;

        if (this.editTemplatesForm.invalid) {
            return;
        }

        this.templatesService.update(this.editTemplatesForm.value)
            .subscribe(
                data => {
                    this.toastr.success(this.translate.instant('settings.templates.messages.update'), this.translate.instant('settings.templates.title'));
                    this.event.emit({data: true});
                    this.onCancel();
                });
    }

    onCancel() {
        this.onClose.next(false);
        this.bsModalRef.hide();
    }
}
