import {Component, EventEmitter, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {BsModalRef} from 'ngx-bootstrap/modal';
import {ToastrService} from 'ngx-toastr';
import {TranslateService} from '@ngx-translate/core';
import {Subject} from 'rxjs';

import {Regions} from '../../../../shared/models/regions.model';

import {RegionsService} from '../../../../core/services/regions.service';
import {HelperService} from '../../../../core/services/helper.service';

@Component({
    selector: 'app-regions-edit',
    templateUrl: './regions-edit.component.html',
    styleUrls: ['./regions-edit.component.scss']
})

export class RegionsEditComponent implements OnInit {
    public event: EventEmitter<any> = new EventEmitter();
    public onClose: Subject<boolean>;
    editRegionsForm: FormGroup;
    formTables: any;
    selectboxOptions: any;
    regions: Regions;
    isFormSubmitted = false;
    isPageLoaded = false;
    editorOptions = {theme: 'vs-dark', language: 'html'};

    constructor(
        public translate: TranslateService,
        public bsModalRef: BsModalRef,
        private formBuilder: FormBuilder,
        private toastr: ToastrService,
        private regionsService: RegionsService,
        private helperService: HelperService
    ) {

    }

    get regionsControl() {
        return this.editRegionsForm.controls;
    }

    ngOnInit() {
        this.onClose = new Subject();
        this.loadForms();
    }

    loadForms() {
        this.editRegionsForm = this.formBuilder.group({
            id: [this.regions.id],
            name: [this.regions.name, Validators.required],
            content_html: [this.regions.content_html, Validators.required],
            number_content: [this.regions.number_content, [Validators.required, Validators.min(1)]],
            description: [this.regions.description],
        });
        this.isPageLoaded = true;

    }

    onSubmit() {
        this.isFormSubmitted = true;

        if (this.editRegionsForm.invalid) {
            return;
        }

        this.regionsService.update(this.editRegionsForm.value)
            .subscribe(
                data => {
                    this.toastr.success(this.translate.instant('settings_web.regions.messages.update'), this.translate.instant('settings_web.regions.title'));
                    this.event.emit({data: true});
                    this.onCancel();
                });
    }

    onCancel() {
        this.onClose.next(false);
        this.bsModalRef.hide();
    }
}
