import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {HttpClient} from '@angular/common/http';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {NgSelectModule} from '@ng-select/ng-select';
import {BsDropdownModule, ModalModule, TabsModule, TimepickerModule, CollapseModule, TooltipModule} from 'ngx-bootstrap';
import {UiSwitchModule} from 'ngx-ui-switch';
import {NgxPermissionsModule} from 'ngx-permissions';
import {NgxEditorModule} from 'ngx-editor';
import {ExportAsModule} from 'ngx-export-as';
import {TranslateLoader, TranslateModule} from '@ngx-translate/core';
import {TranslateHttpLoader} from '@ngx-translate/http-loader';
import {ChecklistModule} from 'angular-checklist';
import {DataTablesModule} from 'angular-datatables';

import {SharedModule} from '../../shared/shared.module';
import {SettingsWebRoutingModule} from './settings-web-routing.module';

import {SettingsWebComponent} from './pages/settings-web/settings-web.component';
import {RegionsComponent} from './components/regions/regions.component';
import {RegionsCreateComponent} from './components/regions-create/regions-create.component';
import {RegionsEditComponent} from './components/regions-edit/regions-edit.component';
import {TemplatesComponent} from './components/templates/templates.component';
import {TemplatesCreateComponent} from './components/templates-create/templates-create.component';
import {TemplatesEditComponent} from './components/templates-edit/templates-edit.component';

import {PagesComponent} from './components/pages/pages.component';
import {PagesCreateComponent} from './components/pages-create/pages-create.component';
import {PagesEditComponent} from './components/pages-edit/pages-edit.component';

import {WidgetsComponent} from './components/widgets/widgets.component';
import {WidgetsCreateComponent} from './components/widgets-create/widgets-create.component';
import {WidgetsEditComponent} from './components/widgets-edit/widgets-edit.component';
import {WidgetsEditDataComponent} from './components/widgets-edit-data/widgets-edit-data.component';

import {ThemesComponent} from './components/themes/themes.component';
import {ThemesCreateComponent} from './components/themes-create/themes-create.component';
import {ThemesEditComponent} from './components/themes-edit/themes-edit.component';
import {ThemesCreateImportComponent} from './components/themes-create-import/themes-create-import.component';

import {WidgetsTypesComponent} from './components/widgets-types/widgets-types.component';
import {WidgetsTypesCreateComponent} from './components/widgets-types-create/widgets-types-create.component';
import {WidgetsTypesEditComponent} from './components/widgets-types-edit/widgets-types-edit.component';


import {ManagerRegionsComponent} from './pages/manager-regions/manager-regions.component';
import {ManagerRegionsCreateComponent} from './components/manager-regions-create/manager-regions-create.component';
import {ManagerRegionsEditComponent} from './components/manager-regions-edit/manager-regions-edit.component';
import {ManagerWidgetsComponent} from './pages/manager-widgets/manager-widgets.component';
import {ManagerWidgetsCreateComponent} from './components/manager-widgets-create/manager-widgets-create.component';
import {ManagerWidgetsEditComponent} from './components/manager-widgets-edit/manager-widgets-edit.component';
import {ManagerWidgetTypesComponent} from './pages/manager-widgets-types/manager-widgets-types.component';
import {ManagerWidgetsTypesCreateComponent} from './components/manager-widgets-types-create/manager-widgets-types-create.component';
import {ManagerWidgetsTypesEditComponent} from './components/manager-widgets-types-edit/manager-widgets-types-edit.component';
import {ManagerFileComponent} from './pages/manager-file/manager-file.component';

import {MonacoEditorModule} from 'ngx-monaco-editor';
import { LightBulbComponent } from 'src/app/shared/components/light-bulb/light-bulb.component';



@NgModule({
    declarations: [
        SettingsWebComponent,
        RegionsCreateComponent,
        RegionsEditComponent,
        RegionsComponent,
        TemplatesCreateComponent,
        TemplatesEditComponent,
        TemplatesComponent,
        WidgetsCreateComponent,
        WidgetsEditComponent,
        WidgetsEditDataComponent,
        WidgetsComponent,
        PagesCreateComponent,
        PagesEditComponent,
        PagesComponent,
        ManagerRegionsComponent,
        ManagerFileComponent,
        ManagerRegionsCreateComponent,
        ManagerRegionsEditComponent,
        ThemesComponent,
        ThemesCreateComponent,
        ThemesCreateImportComponent,
        ThemesEditComponent,
        ManagerWidgetsComponent,
        ManagerWidgetsCreateComponent,
        ManagerWidgetsEditComponent,
        WidgetsTypesComponent,
        WidgetsTypesCreateComponent,
        WidgetsTypesEditComponent,
        ManagerWidgetTypesComponent,
        ManagerWidgetsTypesCreateComponent,
        ManagerWidgetsTypesEditComponent,
        LightBulbComponent
    ],
    imports: [
        CommonModule,
        SettingsWebRoutingModule,
        FormsModule,
        ReactiveFormsModule,
        NgSelectModule,
        ChecklistModule,
        DataTablesModule,
        NgxPermissionsModule,
        NgxEditorModule,
        ExportAsModule,
        TimepickerModule.forRoot(),
        TabsModule.forRoot(),
        ModalModule.forRoot(),
        TooltipModule.forRoot(),
        BsDropdownModule.forRoot(),
        UiSwitchModule.forRoot({
            checkedLabel: 'Active',
            uncheckedLabel: 'Inactive',
            color: 'rgb(0, 189, 99)',
            switchColor: '#fff',
            defaultBgColor: '#c6c6c6',
            defaultBoColor: '#c39ef8'
        }),
        TranslateModule.forChild({
            loader: {
                provide: TranslateLoader,
                useFactory: (HttpLoaderFactory),
                deps: [HttpClient]
            }
        }),
        SharedModule,
        CollapseModule,
        MonacoEditorModule.forRoot()
    ],
    entryComponents: [
        RegionsCreateComponent,
        RegionsEditComponent,
        TemplatesCreateComponent,
        TemplatesEditComponent,
        WidgetsCreateComponent,
        WidgetsEditComponent,
        WidgetsEditDataComponent,
        PagesCreateComponent,
        PagesEditComponent,
        PagesComponent,
        ManagerRegionsCreateComponent,
        ManagerRegionsEditComponent,
        ThemesCreateComponent,
        ThemesCreateImportComponent,
        ThemesEditComponent,
        ManagerWidgetsCreateComponent,
        ManagerWidgetsEditComponent,
        WidgetsTypesCreateComponent,
        WidgetsTypesEditComponent,
        ManagerWidgetsTypesCreateComponent,
        ManagerWidgetsTypesEditComponent,
        LightBulbComponent
    ]
})

export class SettingsModule {
}

// Required for AOT compilation
export function HttpLoaderFactory(http: HttpClient) {
    return new TranslateHttpLoader(http, './assets/i18n/', '.json');
}
