import {Component, EventEmitter, OnInit} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {BsModalRef} from 'ngx-bootstrap/modal';
import {ToastrService} from 'ngx-toastr';
import {TranslateService} from '@ngx-translate/core';
import {Subject} from 'rxjs';

import {ManagerWidgets} from '../../../../shared/models/manager-widgets.model';
import {PagesService} from '../../../../core/services/pages.service';
import {WidgetsService} from '../../../../core/services/widgets.service';
import {HelperService} from '../../../../core/services/helper.service';

@Component({
    selector: 'app-manager-widgets-create',
    templateUrl: './manager-widgets-create.component.html',
    styleUrls: ['./manager-widgets-create.component.scss']
})

export class ManagerWidgetsCreateComponent implements OnInit {
    public event: EventEmitter<any> = new EventEmitter();
    public onClose: Subject<boolean>;
    widgetData: any;
    selectboxOptions: any;
    managerWidgets: ManagerWidgets;
    createManagerWidgetsForm: FormGroup;
    isFormSubmitted = false;
    isPageLoaded = false;
    editorOptions = {theme: 'vs-dark', language: 'html'};

    constructor(
        public translate: TranslateService,
        public bsModalRef: BsModalRef,
        private formBuilder: FormBuilder,
        private toastr: ToastrService,
        private pagesService: PagesService,
        private  widgetsService: WidgetsService,
        private helperService: HelperService
    ) {
    }

    get managerWidgetsControl() {
        return this.createManagerWidgetsForm.controls;
    }

    ngOnInit() {
        this.onClose = new Subject();
        this.loadForms();
        this.getAllWidgets();
    }

    getAllWidgets() {
        this.widgetsService.getAll()
            .subscribe(
                data => {
                    this.widgetData = data;
                });
    }

    loadForms() {
        this.createManagerWidgetsForm = this.formBuilder.group({
            widgets_id: [null, Validators.required],
            index: [null, [Validators.required, Validators.min(0)]],
        });
    }

    onSubmit() {
        this.isFormSubmitted = true;

        if (this.createManagerWidgetsForm.invalid) {
            return;
        }
        this.createManagerWidgetsForm.value.pages_id = this.managerWidgets.pages_id;
        this.createManagerWidgetsForm.value.regions_id = this.managerWidgets.regions_id;
        this.createManagerWidgetsForm.value.regions_number = this.managerWidgets.regions_number;
        this.pagesService.managerWidgets(this.createManagerWidgetsForm.value)
            .subscribe(
                data => {
                    this.toastr.success(this.translate.instant('settings_web.manager_widgets.messages.create'), this.translate.instant('settings_web.manager_widgets.title'));
                    this.event.emit({data: true});
                    this.onCancel();
                });
    }

    onCancel() {
        this.onClose.next(false);
        this.bsModalRef.hide();
    }
}
